.header {
    position: relative;
    max-width: 880px;

    margin: auto;
    padding: 45px 0 41px;
}

@media (max-width: 500px) {
    .header {
        padding: 28px 0 31.6px;
    }
}

.header::before {
    content: '';

    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    
    opacity: 0.7;
    border: 1px solid #545454;
    box-sizing: border-box;
}